.contact-us {
    .title-small {
        margin-bottom: 50px;
    }

    &-form {
        display: flex;
        flex-direction: column;
        flex-flow: column wrap;

        &__content {
            display: flex;
            justify-content: space-between;
            margin-bottom: 50px;

            &-left {
                display: flex;
                flex-direction: column;
                .input-thick {
                    margin-bottom: 30px;
                    &::placeholder {
                        text-transform: uppercase;
                    }
                    &:last-child {
                        margin-bottom: 0;
                    }
                }
            }

            &-right {
                width: 100%;
                padding-left: 40px;

                textarea {
                    width: 100%;
                    height: 100%;
                    text-align: left;
                    resize: none;
                    padding: 10px;
                    &::placeholder {
                        text-align: left;
                    }
                }
            }
        }

        .btn-primary {
            border-radius: 15px;
            min-width: 350px;
            text-transform: none;
            display: block;
            margin: 0 auto;
        }
    }

    @include max-screen(580px) {
        display: flex;
        flex-direction: column;
        align-items: center;

        .title {
            margin-bottom: 20px;
        }
        .title-small {
            text-align: center;
            margin-bottom: 30px;
        }

        &-form {
            width: 100%;
            max-width: 320px;

            &__content {
                flex-direction: column;
                margin-bottom: 30px;
            }

            &__content-right {
                padding-left: 0;
                margin-top: 30px;
            }

            .input-slim {
                width: 100%;
                margin-right: 0;
                margin-bottom: 25px;
            }

            .btn-primary {
                min-width: 100%;
            }

            textarea {
                min-height: 120px;
            }
        }
    }
}
