.input-thick {
    border-radius: 15px;
    border: 1px solid #90A4AE;
    background-color: transparent;
    text-align: center;
    font: {
        family: $m-regular;
        size: 18px;
    }
    color: #90A4AE;
    padding: 15px;
    &::placeholder {
        color: #90A4AE;
        text-align: center;
        font: {
            family: $m-regular;
            size: 18px;
        }
    }
}

.input-slim {
    border: 1px solid transparent;
    border-bottom: 1px solid #546E7A;
    background-color: transparent;
    color: #90A4AE;
    padding: 5px 2px;
    &::placeholder {
        color: #B0BEC5;
        font: {
            family: $m-regular;
            size: 20px;
        }
    }
}

@include max-screen(580px) {
    .input-slim {
        color: #546E7A;
        &::placeholder {
            color: #546E7A;
        }
    }

    .input-thick {
        font-size: 16px;
        padding: 15px;
        &::placeholder {
            font-size: 16px;
        }
    }
}
