.mob-contacts {
    display: none;
}

@include max-screen(580px) {
    .jumbotron-pic-full {
        display: none;
    }

    .mob-contacts {
        display: block;

        .title {
            font-size: 25px;
            margin: 30px 0 10px;
        }
        .paragraph {
            text-align: left;
            margin: 6px 0;
            padding: 0 20px;
        }
    }
}
