.container-scene {
    border-radius: 10px;
    background: url(../../img/bg-scene/bg-scene-01.svg) no-repeat center bottom, radial-gradient(ellipse farthest-corner at center top, #FFFFFF 0%, #CFD8DC 100%);
    background-blend-mode: multiply;
    background-size: auto;
    width: 1440px;
    max-width: 1440px;
    height: 780px;
    max-height: 780px;
    z-index: 12;
    position: relative;
    overflow: hidden;

    //&:after {
    //    content: '';
    //    width: 1440px;
    //    max-width: 1440px;
    //    height: 780px;
    //    max-height: 780px;
    //    background: #78909C;
    //    mix-blend-mode: multiply;
    //    border-radius: 10px;
    //    opacity: 0.5;
    //    filter: blur(20px);
    //    display: block;
    //    z-index: 2;
    //    position: absolute;
    //    right: 0;
    //    left: 0;
    //    top: 10px;
    //}


    @include max-screen(1536px) {
        height: 680px;
        max-height: 680px;
    }

    @include max-screen(1440px) {
        width: 1400px;
        max-width: 1400px;
    }

    @include max-screen(1440px) {
        border-radius: 0;
        width: 100%;
        max-width: 100%;
        height: 100vh;
        max-height: 100vh;
    }
}
