.jumbotron {
    text-align: center;
    margin-top: 20px;

    .title-small {
        padding-bottom: 30px;
    }

    .paragraph {
        max-width: 640px;
        margin: 30px auto 0;
    }

    .jumbotron-pic {
        margin: 20px auto 30px;
    }

    .jumbotron-pic-full {
        width: 85%;
        margin-top: 30px;
    }

    @include max-screen(768px) {
        .jumbotron-pic-full {
            width: 100%;
        }
    }

    @include max-screen(580px) {
        .title-small {
            padding-bottom: 0;
        }

        .jumbotron-pic {
            margin-bottom: 0;
        }

        .paragraph {
            margin: 20px 0;
        }
    }
}
