.wrapper {
    background: url(../../img/bg/bg-wrapper.svg) no-repeat center bottom, transparent radial-gradient(closest-side at 50% 0%, #ECEFF1 0%, #90A4AE 100%) 0 0 no-repeat padding-box;
    background-blend-mode: multiply;
    background-size: auto;
    height: 100vh;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
