.subscribe {

    .title {
        margin: 20px 0 50px;
    }

    &-form {
        max-width: 320px;
        display: flex;
        flex-direction: column;
        margin: auto;

        .input-thick {
            width: 100%;
            margin-bottom: 30px;
        }

        .btn-primary {
            border-radius: 15px;
            width: 100%;
            text-transform: none;
            font-size: 18px;
        }

        @include max-screen(580px) {
            .input-thick {
                margin-bottom: 20px;
            }
            .btn-primary {
                font-size: 16px;
            }
        }
    }
}
