.team {
    .paragraph {
        line-height: 28px;
        font-size: 18px;
        padding: 0;
        margin: 0;
    }

    .title-small {
        margin-bottom: 20px;
    }

    .mob-team-slider {
        display: none;
    }

    @include max-screen(1536px) {
        .tabs-content-img {
            width: 250px;
            max-width: 250px;
        }
    }

    @include max-screen(994px) {
        .tabs-content-img {
            display: none;
        }
    }

    @include max-screen(580px) {
        .tabs {
            display: none;
            margin-top: 20px;

            &-navigation {
                margin-bottom: 30px;
            }
        }

        .mob-team-slider {
            display: block;
            position: relative;

            //.slide {
            //    width: 100% !important;
            //}

            .title {
                font-size: 20px;
                margin-bottom: 0;
            }

            .paragraph {
                font-size: 14px;
                line-height: 20px;
                margin-top: 12px;
                padding: 0 15px;
            }
        }
    }
}
