.sidebar {
    height: 100%;
    min-height: 100%;
    display: flex;
    position: relative;
    &-navigation {
        margin-top: 100px;
        &__item {
            padding-left: 35px;
            margin-bottom: 20px;
            transition: .8s all;
            position: relative;
            &:before {
                content: '';
                border-radius: 50%;
                background-color: #37474F;
                width: 15px;
                height: 15px;
                opacity: 0;
                transition: .8s all;
                position: absolute;
                bottom: 0;
                left: 0;
                top: 2px;
            }
            &-active {
                transition: .8s all;
                font-family: $m-semiBold;
                color: #37474F;
                &:before {
                    content: '';
                    border-radius: 50%;
                    background-color: #37474F;
                    width: 15px;
                    height: 15px;
                    opacity: 1;
                    transition: .8s all;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    top: 2px;
                }
            }
            &:last-child {
                margin-bottom: 0;
            }
            &:hover {
                transition: .8s all;
                font-family: $m-semiBold;
                &:before {
                    content: '';
                    border-radius: 50%;
                    background-color: #37474F;
                    width: 15px;
                    height: 15px;
                    opacity: 1;
                    transition: .8s all;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    top: 2px;
                }
            }
        }

        .sidebar-navigation-drop__active {
            display: flex;
            flex-direction: column;
        }

        &-drop {
            display: none;
            margin: 25px 0;
            &-none {
                display: none;
            }

            &__active {
                display: block;
            }

            .sidebar-navigation__item {
                font: {
                    family: $m-regular;
                    size: 18px;
                }
                padding-left: 35px;
                margin-bottom: 18px;
                position: relative;

                &:last-child {
                    margin-bottom: 0;
                }
                &:before {
                    content: '';
                    border-radius: 50%;
                    background-color: #37474F;
                    width: 6px;
                    height: 6px;
                    opacity: 0;
                    transition: .8s all;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    top: 6px;
                }
                &:hover {
                    transition: .8s all;
                    font-family: $m-semiBold !important;
                    color: #37474F;
                    &:before {
                        content: '';
                        border-radius: 50%;
                        background-color: #37474F;
                        width: 6px;
                        height: 6px;
                        opacity: 1;
                        transition: .8s all;
                        position: absolute;
                        bottom: 0;
                        left: 0;
                        top: 6px;
                    }
                }
            }
            .sidebar-navigation__item-active,
            .first-active {
                color: #37474F;
                transition: .8s all;
                font-family: $m-semiBold !important;
                &:before {
                    content: '';
                    border-radius: 50%;
                    background-color: #37474F;
                    width: 6px;
                    height: 6px;
                    opacity: 1;
                    transition: .8s all;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    top: 6px;
                }
            }
        }
    }

    .mob-navigation-social-network,
    .mob-elem {
        display: none;
    }

    @include max-screen(768px) {
        &-navigation {
            &__item {
                padding-left: 25px;
            }
            &-drop__item {
                padding-left: 15px;
            }
            a {
                font-size: 18px;
            }
        }
    }
}

@include max-screen(680px) {
    .mob-sidebar-none,
    .mob-elem {
        display: none;
    }
}

@include max-screen(580px) {
    .mob-navigation-social-network {
        display: flex !important;
    }

    .mob-sidebar-block {
        display: block;
    }

    .mob-sidebar-block,
    .mob-sidebar-none {
        padding: 0;
        z-index: 1001;
        position: absolute;
        bottom: 0;
        right: 0;
        left: 0;
        top: 0;
    }

    .sidebar {
        background: url(../../../img/bg/414x896.png) no-repeat center bottom, radial-gradient(ellipse farthest-corner at center top, #B0BEC5 0%, #78909C 100%);
        background-blend-mode: soft-light;
        background-size: cover;
        width: 100%;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        z-index: 999;
        padding: 40px 40px 80px 40px;

        &-navigation {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-top: 0;
            &__item {
                width: 100%;
                text-align: center;
                padding: 0;
                margin-bottom: 30px;
                a {
                    font-size: 16px;
                    color: #ECEFF1;
                }
                &:before {
                    display: none;
                }

                &:last-child {
                    margin-bottom: 0;
                    a {
                        color: #eeb0b6;
                    }
                }
            }

            &-drop {
                margin: 20px 0 0;
                .sidebar-navigation__item {
                    font: {
                        family: $m-light;
                        size: 14px;
                    }
                    padding: 0;
                    margin: 0 5px 0 0;
                    &:before {
                        display: none;
                    }

                    &:last-child {
                        margin-right: 0;
                        a {
                            color: #ECEFF1;
                        }
                    }
                }
                &__active {
                    flex-direction: row !important;
                }
            }
        }
    }
}
