.header {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding: 45px 0 30px;

    &-navigation {
        position: relative;
        display: flex;

        &-drop {
            display: none;
            z-index: 1002;
            position: absolute;
            bottom: -140px;
            right: 0;
            left: 0;

            &__item {
                cursor: pointer;
                font: {
                    family: $m-light;
                    size: 16px;
                }
                display: inline-block;
                margin-bottom: 15px;

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }

        &-drop-lang {
            display: none;
            z-index: 1002;
            position: absolute;
            bottom: -30px;
            right: 0;
            left: 0;
        }

        &-drop-lang-active {
            display: block;
        }

        &-drop-active {
            display: block;
        }

        &__item {
            cursor: pointer;
            position: relative;

            .drop-menu-icon {
                transition: .6s all;
                margin-left: 8px;
            }

            .drop-menu-icon-active {
                transition: .6s all;
                transform: rotate(90deg);
            }

            &:nth-child(2n) {
                font-family: $m-semiBold;
                margin-left: 60px;
            }

            &-absolute {
                width: 50px;
                cursor: pointer;
                text-align: center;
                font: {
                    family: $m-light;
                    size: 16px;
                }
                color: #78909C;
                position: absolute;
                right: -60px;
                top: 4px;

                &:before {
                    content: '';
                    border: 1px solid #b2b2b2;
                    width: 30px;
                    height: 19px;
                    opacity: 0;
                    position: absolute;
                    bottom: 0;
                    right: 0;
                    left: 9px;
                    top: -2px;
                }

                &:hover:before  {
                    opacity: 1;
                }
            }
        }
    }

    .logo {
        opacity: 1;
        transition: .5s all;
    }

    @include max-screen(1024px) {
        &-navigation__item-absolute {
            margin-left: 40px;
            position: relative;
            right: 0;
        }
    }

    @include max-screen(768px) {
        padding: 15px 0 30px;
    }
}

.mob-header,
.mob-navigation,
.mob-navigation-header {
    display: none;
}

.logo-none {
    opacity: 0 !important;
    transition: .5s all !important;
}

@include max-screen(580px) {
    .mob {
        display: flex;
        justify-content: space-between;
        padding: 20px 0;

        .logo {
            width: 140%;
        }

        &-header {
            display: flex;
            justify-content: space-between;
            padding: 30px 10px;

            .header-logo img {
                width: 160%;
                transition: .5s all;
            }
        }

        &-navigation {
            //width: 100%;
            //min-height: 100vh;
            //display: flex;
            //flex-direction: column;
            //justify-content: space-between;
            //z-index: 999;
            //padding: 40px;
            //position: absolute;
            //bottom: 0;
            //right: 0;
            //left: 0;
            //top: 0;

            &-header {
                display: flex;
                justify-content: flex-end;
                position: relative;
                min-height: 22px;

                .btn-menu-close {
                    position: absolute;
                    right: -22px;
                    top: -6px;
                }
            }

            .mob-navigation-list {
                width: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;

                .sidebar-navigation__item {
                    text-align: center;
                    padding: 0;
                    margin-bottom: 30px;

                    a {
                        font-size: 16px;
                        color: #ECEFF1;
                    }

                    &:before {
                        display: none;
                    }

                    &:last-child {
                        margin-bottom: 0;

                        a {
                            color: #eeb0b6;
                        }
                    }
                }

                .sidebar-navigation__item-active {
                    font-family: $m-semiBold;
                }
            }

            &-social-network {
                width: 100%;
                display: flex;
                flex-wrap: wrap;
                align-content: flex-start;
                justify-content: center;

                &__item {
                    text-align: center;
                    width: 30%;
                    margin-top: 30px;
                }
            }
        }
    }
}
