.section-0 {
    opacity: 1;
}
.section-1,
.section-2,
.section-3,
.section-4,
.section-5,
.section-6,
.section-7 {
    opacity: 0;
}

.section-hide {
    opacity: 0;
    transition: .5s all;
    position: absolute;
    right: 0;
    left: 0;
    top: 120px;
}

.section-show {
    opacity: 1;
    z-index: 999;
    transition: 1s all;
    position: absolute;
    right: 0;
    left: 0;
    top: 0;
}
