.btn {
    border: 1px solid;
    cursor: pointer;
    display: inline-block;
}

.btn-primary {
    border-radius: 6px;
    border: 1px solid #FF5252;
    background: linear-gradient(to left, #FF7675, #FF5252);
    color: #fff;
    text-transform: uppercase;
    padding: 18px 26px 15px;
}

.btn-link {
    border-width: 0;
    background: transparent;
    outline: none;
    cursor: pointer;
    padding: 0;
    margin: 0;
}

@include max-screen(580px) {
    .btn-primary {
        padding: 15px 26px;
    }
}


