//}

.title {
    text-align: center;
    font:{
        family: $m-bold;
        size: 50px;
    }
    margin-bottom: 5px;
}

.title-small {
    text-align: center;
    font:{
        family: $m-semiBold;
        size: 18px;
    }
}

.paragraph {
    text-align: left;
    line-height: 24px;
    padding-left: 15px;
}

.paragraph-imposed {
    border-left: 1px solid #CFD8DC;
}

.paragraph-center {
    text-align: center;
    padding: 0;
}

@include max-screen(768px) {
    .title {
        font-size: 45px;
    }

    .paragraph {
        font-size: 18px;
    }
}

@include max-screen(580px) {
    .title {
        font-size: 25px;
    }

    .title-small {
        font-size: 16px;
    }

    .paragraph {
        font-size: 14px;
        text-align: center;
        line-height: 20px;
        padding: 0 15px;
        margin: 20px 0;
    }
}
