h1, h2, h3, h4, h5, h6, ul {
    margin: 0;
    padding: 0;
}

body {
    //background: url(../../img/bg/1680x1050.png) no-repeat center bottom -160px, radial-gradient(ellipse farthest-corner at center top, #ECEFF1 0%, #90A4AE 100%);
    //background-blend-mode: soft-light;
    //background-size: cover;
    //height: 100vh;
    //min-height: 100vh;
    overflow: hidden;
    font-family: $m-regular;
    font-size: 20px;
    color: #37474F;
    position: relative;
}

section {
    height: 100%;
    min-height: 100%;
}

a {
    text-decoration: none;
    outline: none;
    color: #546E7A;
}

a:active {
    outline: none;
}

a:focus {
    outline: none;
}

.red-link {
    color: #FF5252;
}

.mobile-version {
    display: none;
}

@include max-screen(580px) {
    .mob-none {
        display: none;
    }
}

