.tabs {
    &-navigation {
        margin-bottom: 40px;
        &-list {
            display: flex;
            justify-content: center;
            &__item {
                font: {
                    family: $m-light;
                    size: 18px;
                }
                cursor: pointer;
                margin-right: 52px;
                &:last-child {
                    margin-right: 0;
                }

                &-active {
                    font: {
                        family: $m-semiBold;
                        size: 18px;
                    }
                    position: relative;

                    &:after {
                        content: '';
                        border-radius: 50%;
                        background-color: #546E7A;
                        width: 5px;
                        height: 5px;
                        display: block;
                        position: absolute;
                        right: 0;
                        left: calc(55% - 5px);
                        bottom: -12px;
                    }
                }
            }
        }
    }

    &-content {
        display: none;

        &-active {
            display: flex;
            visibility: visible;
            min-height: 300px;
        }

        .title {
            font-size: 40px;
            text-align: left;
            margin-bottom: 20px;
        }

        &-img {
            margin-right: 40px;
        }
    }

    @include max-screen(768px) {
        &-content-img {
            display: none;
        }
    }
}
