.main {
    height: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    &-content {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &-swipe-down {
        font-size: 14px;
        flex-direction: column;
        align-items: center;
        display: none;
        position: absolute;
        bottom: 40px;
        img {
            margin-top: 10px;
        }
    }

    &-title {
        font: {
            family: $font-circule-regular;
            size: 50px;
        }
        color: #37474F;
        text-transform: uppercase;
        margin: 20px 0 10px;
    }

    &-description {
        color: #78909C;
    }

    &-description-light {
        color: #37474F;
        margin: 25px 0;
    }

    .btn-primary {
        font-family: $m-semiBold;
    }

    @include min-screen(1024px) {
        width: 90%;
    }

    @include max-screen(580px) {
        .btn-primary {
            margin-top: 40px;
        }

        &-swipe-down {
            display: flex;
        }
    }
}

.full-pos {
    position: relative;
}
