.support {
    .title {
        margin: 20px 0 70px;
    }
    &-list {
        display: flex;
        justify-content: center;
        margin-bottom: 70px;
        &:last-child {
            margin-bottom: 0;
        }
        &-item {
            width: 163px;
            min-height: 122px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
            position: relative;
            &:hover {
                .support-list-item__icon {
                    margin-top: -12px;
                    opacity: 1;
                    background: linear-gradient(to bottom, #FF7675, #FF5252);
                }
                .support-list-item__title {
                    color: #546E7A;
                    &:before {
                        content: '';
                        border-radius: 50%;
                        background-color: #FF5252;
                        width: 5px;
                        height: 5px;
                        display: block;
                        position: absolute;
                        opacity: 1;
                        right: 0;
                        left: calc(55% - 5px);
                        top: -12px;
                    }
                }
            }
            &__title {
                color: #90A4AE;
                font: {
                    family: $m-semiBold;
                    size: 16px;
                }
                margin-top: 25px;
                position: relative;
                transition: 1s all;
                &:before {
                    content: '';
                    border-radius: 50%;
                    background-color: #FF5252;
                    width: 5px;
                    height: 5px;
                    display: block;
                    position: absolute;
                    opacity: 0;
                    right: 0;
                    left: calc(55% - 5px);
                    top: -12px;
                    transition: 1s all;
                }
            }
            &__icon {
                width: 70px;
                height: 80px;
                opacity: 0.5;
                background: linear-gradient(to top, #556672, #B0BEC5);
                transition: 0.5s all;
            }
        }
    }
}

.mob-support {
    display: none;
}

@include max-screen(580px) {
    .support {
        display: none;
    }

    .mob-support {
        display: block;
        position: relative;

        &-slide {
            display: flex !important;
            flex-direction: column;
            align-items: center;
            padding-top: 20px;
        }

        .slider-btn-left,
        .slider-btn-right {
            top: 45px;
        }
    }
}
