.social-network {
    margin-top: 100px;
    &-list {
        display: flex;
        flex-direction: column;
        &__item {
            text-align: right;
            transition: .5s all;
            margin-bottom: 30px;
            position: relative;
            &:after {
                content: '';
                border-radius: 50%;
                background-color: #FF5252;
                width: 4px;
                height: 4px;
                opacity: 0;
                transition: .6s all;
                position: absolute;
                right: 0;
                top: 49%;
            }
            &:last-child {
                margin-bottom: 0;
            }
            &:hover {
                padding-right: 12px;
                transition: .5s all;
                &:after {
                    content: '';
                    border-radius: 50%;
                    background-color: #FF5252;
                    width: 4px;
                    height: 4px;
                    opacity: 1;
                    transition: .6s all;
                    position: absolute;
                    right: 0;
                    top: 49%;
                }
            }
        }
    }
}
