.slick-arrow {
    opacity: 1;
    transition: 1s all;
}

.slick-disabled {
    opacity: 0;
    transition: 1s all;
}

.slider-btn-left {
    border-width: 0;
    background-color: transparent;
    z-index: 12;
    position: absolute;
    left: 10px;
    top: 0;
}

.slider-btn-right {
    border-width: 0;
    background-color: transparent;
    z-index: 12;
    position: absolute;
    right: 10px;
    top: 0;
}
